.title-header {
  position: absolute;
  top: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 10;
  width: 200px;
  text-align: center;
}
.title-header h1 {
  display: inline-block;
  color: #3290ff;
  vertical-align: middle;
  margin: 0 20px 0 0;
}
.title-header .title-input {
  position: absolute;
  top: 4px;
  left: -20px;
}
.title-header .title-btn {
  display: none;
  vertical-align: middle;
}
.title-header:hover .title-btn {
  display: inline-block;
}
